<template>
  <CButton color="primary" @click="addNewPost"> </CButton>
  <CModal size="xl" backdrop="static" alignment="center" :visible="visible" @close="closeModal">
    <CForm novalidate :validated="formValid" ref="add-file-form" @change="wregert"
      @submit.prevent="mode === 'new' ? addNewing() : editNewing()" class="add-user" style="display: 'none'">
      <CModalHeader>
        <CModalTitle>{{
          mode === 'new' ? ' ' : ' '
        }}</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <CFormInput class="mb-3" v-model="formData.title" placeholder="" id="inputHeader"
          aria-describedby="inputGroupPrepend" feedbackValid=" " feedbackInvalid="  "
          required />
        <div class="projects-list">
          <span></span>
          <CFormCheck v-for="project in projects" :key="project.name" :id="project.name"
            :checked="project.name === formData.category_name" @change="changeP" type="radio" name="project-name"
            :value="project.name" :label="project.name" />
          <CFormCheck id="null-name" :checked="!formData.category_name" @change="changeP" type="radio" name="project-name"
            value="" label=" " />
        </div>
        <CFormInput type="number" class="mb-3" label="" placeholder=" " v-model.number="formData.price" />
        <div class="options-shedle">
          <span></span>
          <CFormCheck id="project.name" v-model="formData.select_name" type="radio" name="project-name" value="taste"
            label="" />
          <CFormCheck id="project.name" v-model="formData.select_name" type="radio" name="project-name" value="color"
            label="" />
          <CFormCheck id="project.name" v-model="formData.select_name" type="radio" name="project-name" value=""
            label=" " checked />
          <div class="option-item" v-for="option, id in formData.options_array" :key="option.id">
            <CFormInput type="text" class="mb-3" label="" placeholder=" "
              v-model="formData.options_array[id].name" />
            <CFormInput type="number" class="mb-3" label="" placeholder=" "
              v-model="formData.options_array[id].stock" />
            <CFormInput type="file" accept="image/*" multiple="multiple" ref="file"
              @change="previewMultiImage(id, $event)" class="mb-3" label="" placeholder="" />
            <div class="border p-2 mt-3 preview-container">
              <template v-if="preview_list?.[id]?.length">
                <div v-for="item, index in preview_list?.[id]" :key="index">
                  <img :src="item" class="img-fluid" />
                  <button @click.prevent="dropFile(id, index)"></button>
                </div>
              </template>
            </div>
          </div>
          <CButton color="primary" type="button" @click="formData.options_array.push({})">  </CButton>
        </div>



        <QuillEditor theme="snow" toolbar="essential" ref="postTextEditor" id="postTextEditor"
          placeholder=" " />
      </CModalBody>
      <CModalFooter>
        <CButton color="secondary" @click="closeModal">  </CButton>
        <CButton v-show="mode === 'new'" color="primary" type="submit"></CButton>
        <CButton v-show="mode === 'edit'" color="primary" type="submit"></CButton>
      </CModalFooter>
    </CForm>
  </CModal>
</template>

<script>
import axios from 'axios'
const myApi = axios.create({
  withCredentials: true,
})
import { QuillEditor } from '@vueup/vue-quill'
import '@vueup/vue-quill/dist/vue-quill.snow.css'
import TurndownService from 'turndown'
import { marked } from 'marked'
//import * as DOMPurify from 'dompurify'
import eventBus from '../eventBus'

export default {
  components: { QuillEditor },
  props: {
    mode: {
      required: true,
      type: String,
      validator: (value) => ['new', 'edit'].includes(value.toLowerCase()),
    },
    visible: false,
    formData: {
      title: '',
      text: '',
      description: '',
      preview: '',
      stock: 0,
      price: null,
      category_name: '',
      select_name: "",
      image_list: [],
      tags_array: new Set(),
      options_array: [],
    },

  },
  data() {
    return {
      formValid: false,
      preview_list: [],
    }
  },
  updated() {
    console.log(1)

    this.formData.options_array = this.formData.options_array?.
      filter(({ material, price, size }) => !material && !size && !price ? false : true)

    this.formData.description && this.$refs.postTextEditor.pasteHTML(
      marked.parse(this.formData.description?.replaceAll("\r\n\r\n", "<span><br/><span/>\r\n\r\n")))

    for (let optionIndex in this.formData.options_array) {
      this.preview_list[optionIndex] = this.formData.options_array[optionIndex].photos?.map(preview_name => `${this.$store.state.publicPath}/public/pics/${preview_name}`)

    }
  },
  async mounted() {
    this.projects = await this.getProjects()
  },
  methods: {
    async getProjects() {
      return await myApi
        .get(this.$store.state.publicPath + '/api/categories/')
        .then((res) => {
          return res.data
        })
        .catch((error) => {
          eventBus.$emit('noresponse', error)
        })
    },
    previewMultiImage(id, event) {
      var input = event.target;
      var count = input.files.length;
      var index = 0;
      this.formData.preview = input.files[0]
      if (!this.preview_list) this.preview_list = []
      if (!this.preview_list[id]) this.preview_list[id] = []

      if (!this.formData.options_array[id].photos) this.formData.options_array[id].photos = []

      if (input.files) {
        while (count--) {
          var reader = new FileReader();
          reader.onload = (e) => {
            this.preview_list[id].push(e.target.result);
          }
          this.formData.options_array[id].photos.push(input.files[index]);
          reader.readAsDataURL(input.files[index]);
          index++;
        }
      }
    },
    dropFile(id, index) {
      this.formData.options_array[id].photos?.splice(index, 1);
      this.preview_list[id]?.splice(index, 1);

    },
    addNewPost() {
      eventBus.$emit('addNewPost')
    },
    changeP(e) {
      this.formData.category_name = e.target.value
    },
    closeModal() {
      eventBus.$emit('closeModal')
    },
    constractFromData(isEdit) {
      if (!this.formData.title || !this.$refs.postTextEditor.getHTML())
        throw new Error()

      var formData = new FormData()

      formData.append('title', this.formData.title)
      formData.append('price', this.formData.price)
      formData.append('select_name', this.formData.select_name)


      const turndownService = new TurndownService({
        headingStyle: "atx",
        //bulletListMarker: "-",
        //fence: "~~~",
        //emDelimiter: "*",
      })
      formData.append(
        'description',
        turndownService.turndown(this.$refs.postTextEditor.getHTML()),
      )

      this.formData.image_list?.forEach(v => {
        formData.append('images[]', v);
      });

      this.formData.category_name &&
        formData.append('categoryName', this.formData.category_name)

      formData.append('optionsObject', JSON.stringify(this.options_object))
      formData.append('optionsObjectBackside', JSON.stringify(this.options_object_backside))


      isEdit && formData.append('id', this.formData.id)

      return formData
    },
    addNewing() {
      try {
        const formData = this.constractFromData()

        myApi
          .post(this.$store.state.publicPath + '/api/admin/items', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          })
          .then(() => {
            eventBus.$emit('postAdded')
          })
          .catch((e) => {
            eventBus.$emit('noresponse', e)
          })
      } catch (e) {
        this.formValid = true
        //eventBus.$emit('wrongInputData', e)
      }
    },
    editNewing() {
      try {
        const formData = this.constractFromData(true)

        myApi
          .put(this.$store.state.publicPath + '/api/admin/items', formData, {
            headers: {
              'Content-Type': `multipart/form-data`,

            },
          })
          .then(() => {
            eventBus.$emit('postEdited')
          })
          .catch((e) => {
            eventBus.$emit('noresponse', e)
          })
      } catch (e) {
        console.log(e)
        this.formValid = true
        //eventBus.$emit('wrongInputData', e)
      }
    },
  },
}
</script>

<style lang="scss" scoped>
::v-deep .hidden {
  display: none;
}

::v-deep .ql-toolbar.ql-snow {
  display: block !important;
}

.table {
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #dddddd;
  border-collapse: collapse;
}

.table th {
  font-weight: bold;
  padding: 5px;
  background: #efefef;
  border: 1px solid #dddddd;
}

.table td {
  border: 1px solid #dddddd;
  padding: 5px;
  position: relative;

  .btn.btn-secondary {
    position: absolute;
    top: 5px;
    right: 5px;
  }
}

.add-button {
  margin: 0;
}

.preview-container {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;

  &>div {
    max-width: 24%;
    position: relative;

    button {
      position: absolute;
      top: 5px;
      right: 5px;
    }
  }
}

.tags-cloud,
.projects-list {
  display: flex;
  flex-wrap: wrap;

  &>* {
    margin-right: 20px;
  }

  &>span {
    flex: 0 0 100%;
    margin-bottom: 10px;
  }
}
</style>
